/* MaterialDesignIcons Fonts */
@font-face {
    font-family: "Material Design Icons";
    src: url("../assets/mdi/fonts/materialdesignicons-webfont.eot?v=1.9.32");
    src: url("../assets/mdi/fonts/materialdesignicons-webfont.eot?#iefix&v=1.9.32") format("embedded-opentype"), url("../assets/mdi/fonts/materialdesignicons-webfont.woff2?v=1.9.32") format("woff2"), url("../assets/mdi/fonts/materialdesignicons-webfont.woff?v=1.9.32") format("woff"), url("../assets/mdi/fonts/materialdesignicons-webfont.ttf?v=1.9.32") format("truetype"), url("../assets/mdi/fonts/materialdesignicons-webfont.svg?v=1.9.32#materialdesigniconsregular") format("svg");
    font-weight: normal;
    font-style: normal;
}

%mdi-fonts {
    display: inline-block;
    font: normal normal normal 24px/1 "Material Design Icons";
    font-size: inherit;
    text-rendering: auto;
    line-height: inherit;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.chart {
    &-legend {
        ul {
            padding: 0px;
            margin: 32px 0px;
            text-align: center;
            list-style: none;

            li {
                margin: 5px 0px;

                span {
                    display: inline-block;
                    margin-right: 5px;
                    width: 10px;
                    height: 10px;
                }
            }
        }
    }
}
.section-listing {
    .dataTable {
        width: 100% !important;
        margin: 15px auto !important;
        display: inline-table;
        border-bottom: 0px;
        border-collapse: collapse;

        thead {
            tr > th {
                padding: 5px 10px;
                background-color: $primary-color;
                color: #fff;
                border-bottom: 1px solid darken($primary-color, 10%);
                vertical-align: middle;

                > label {
                    margin: 0px;

                    input [type='checkbox'] {
                        margin: 0px;
                    }
                }
            }
        }

        tbody {
            tr.selected {
                background-color: #FFFBED !important;
            }

            tr.unread {
                background-color: $light-background;

                > td {
                    color: $dark-text-color;
                    font-weight: $md-font-weight;

                    > a {
                        font-weight: $sm-font-weight;
                    }
                }
            }

            tr > td {
                padding: 5px 10px;
                vertical-align: middle;
                border-bottom: 1px solid $divider-color;

                > label {
                    margin: 0px;

                    input [type='checkbox'] {
                        margin: 0px;
                    }
                }
            }
        }

        &.no-footer {
            border-bottom: none;
        }

        &.dtr-inline.collapsed > tbody > tr[role="row"] > td:first-child:before,
        &.dtr-inline.collapsed > tbody > tr[role="row"] > th:first-child:before {
            top: 12px;
            box-shadow: 0px 0px 0px 1px #ddd;
        }
    }
    .dataTables {
        &_wrapper {
            .dataTables_filter {
                input[type="search"] {
                    background: $light-background;
                    border: 1px solid $divider-color;
                    height: 34px;
                    padding: 6px 12px;
                    font-size: 14px;
                    line-height: 1.42857143;
                    color: #555;
                    border-radius: 4px;

                    &:focus {
                        border-color: $primary-color;
                        outline: none;
                    }
                }
            }

            .dataTables_length {
                select {
                    height: 34px;
                    padding: 6px 12px;
                    font-size: 14px;
                    line-height: 1.42857143;
                    color: #555;
                    background-color: #fff;
                    background-image: none;
                    border: 1px solid #ccc;
                    border-radius: 4px;

                    &:focus {
                        border-color: $primary-color;
                        outline: none;
                    }
                }
            }

            .dataTables_paginate {
                .paginate_button {
                    background: $shade-color;
                    border: 1px solid $divider-color;
                    border-radius: 50% !important;

                    &:hover,
                    &:focus,
                    &:active,
                    &:visited {
                        border-color: darken($primary-color, 10%);
                        background: darken($primary-color, 10%);
                        box-shadow: none;
                        color: #fff !important;
                    }

                    &.disabled,
                    &.disabled:hover,
                    &.disabled:focus,
                    &.disabled:active,
                    &.disabled:visited {
                        background: $background;
                        border: 1px solid $divider-color;
                        color: $light-text-color !important;
                        cursor: not-allowed;
                    }

                    &.current {
                        background: $primary-color;
                        border-color: darken($primary-color, 10%);
                        color: #fff !important;

                        &:hover,
                        &:focus,
                        &:active,
                        &:visited {
                            border-color: darken($primary-color, 10%);
                            background: darken($primary-color, 10%);
                            color: #fff !important;
                        }
                    }

                    &.previous,
                    &.next {
                        border-radius: 20px !important;
                        width: 100px !important;
                        border: 1px solid $divider-color;
                    }
                }
            }

            .dt-buttons {
                margin-left: 15px;

                .dt-button {
                    &.button-notification {
                        border-radius: 3px;
                        text-transform: uppercase;
                        border-color: $divider-color;
                        color: $text-color;
                    }
                }
            }
        }
    }
}
.section-reporting {

}

.js #loader {
    display: block;
    position: absolute;
    left: 100px;
    top: 0;
}
.se-pre-con {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background: url(../img/preloader.gif) center no-repeat #fff;
}

.error{
    color: $danger-color;
    font-weight: $sm-font-weight;
}
input[type=text].error, input[type=text].error:focus,
input[type=password].error, input[type=password].error:focus,
input[type=email].error, input[type=email].error:focus,
input[type=number].error, input[type=number].error:focus,
select.error, select.error:focus,
textarea.error, textarea.error:focus {
    border-color: $danger-color;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px lighten($danger-color, 20%);
}

.ui-datepicker {
    width: 215px !important;
    margin-top: 1px;
    border: none !important;
    border-radius: 0px !important;
    padding: 0px !important;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.2);

    .ui-datepicker-header {
        padding: 2px 0;
        background: $primary-color;
        border: none;
        border-bottom: 1px solid $primary-color;
        border-radius: 0px;

        .ui-datepicker-prev,
        .ui-datepicker-next {
            width: 30px;
            height: 30px;
            font-size: 20px;
            border: none;
            color: #fff;

            &:hover,
            &:focus,
            &:visited,
            &:active {
                font-size: 20px;
                border: none !important;
                background: transparent !important;
            }

            .ui-icon {
                display: none;
                background-image: none;
            }
        }

        .ui-datepicker-prev {
            text-indent: inherit;
            left: 5px;
            top: 2px;

            &:after {
                content: "\f141";
                @extend %mdi-fonts;
            }

            &:hover,
            &:focus,
            &:visited,
            &:active {
                left: 5px;
                top: 2px;
            }
        }

        .ui-datepicker-next {
            text-indent: inherit;
            right: 0px;
            top: 2px;

            &:after {
                content: "\f142";
                @extend %mdi-fonts;
            }

            &:hover,
            &:focus,
            &:visited,
            &:active {
                right: 0px;
                top: 2px;
            }
        }

        .ui-datepicker-month,
        .ui-datepicker-year {
            font-size: 12px;
            font-weight: 400;
            background-color: $primary-color;
            color: #fff;
            border: none;

            > option {
                background-color: #fff;
                color: #333;
            }
        }
    }

    .ui-datepicker-calendar {
        font-size: 12px;

        thead tr th {
            color: #333;
            font-weight: 600;
        }

        .ui-state-default {
            text-align: center;
            border: none;
            background: transparent;
            color: $primary-color;

            &:hover {
                background: #eee;
            }

            &.ui-state-active {
                background: $primary-color !important;
                color: #fff !important;
            }

            &.ui-state-highlight {
                background: lighten($primary-color, 35%);
                color: $primary-color;
            }
        }
    }
}
.ui-menu {
    &.ui-widget-content {
        border: 1px solid #ccc;
    }
    .ui-menu-item-wrapper{
        padding: 5px 10px;
    }
}

#ndp-nepali-box {
    width: 215px;
    border-radius: 0px !important;
    padding: 0px !important;
    border: none !important;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.2);

    .ndp-header {
        line-height: 28px;
        border-radius: 0px;
        margin: 0px;
        background: none;
        background-color: $primary-color;
        border-color: $primary-color;
        font-size: 20px;

        #currentMonth {
            #npd-month-select,
            #npd-year-select {
                background-color: $primary-color;
                color: #fff;
                border: none;

                > option {
                    background-color: #fff;
                    color: #000;
                }
            }
        }

        .ndp-prev,
        .ndp-next {
            background: none;
            width: 25px;
            height: 25px;
            line-height: 25px;
            color: #fff !important;
            top: 2px !important;

            &:hover,
            &:focus {
                background: darken($primary-color, 5%);
            }
        }

        .ndp-next {
            &:after {
                content: "\f142";
                @extend %mdi-fonts;
            }
        }

        .ndp-prev {
            &:after {
                content: "\f141";
                @extend %mdi-fonts;
            }
        }
    }

    #npd-table-div {
        table {
            width: 100%;
        }

        .ndp-days {
            font-weight: 600px;
        }

        .ndp-date {
            border: none !important;
            background: transparent !important;
            text-align: center;

            > a {
                color: $primary-color;
            }

            &:hover {
                background-color: #eee !important;
            }
        }

        .ndp-current {
            background-color: $primary-color;
            border-color: $primary-color;
            text-align: center;

            > a {
                color: #fff;
            }
        }

        .ndp-selected {
            background-color: lighten($primary-color, 35%);
            border-color: lighten($primary-color, 35%);
            text-align: center;
        }
    }
}

.owl-offer {
    position: relative;

    .owl-dots {
        position: absolute;
        bottom: 10px;
        left: 0px;
        right: 0px;
        text-align: center;

        .owl-dot {
            height: 10px;
            width: 10px;
            margin: 0px 3px;
            border-radius: 50%;
            background-color: #fff;
            box-shadow: 0px 0px 3px 0px #000;
            opacity: 0.7;

            &.active {
                opacity: 1;
            }
        }
    }

    .owl-nav {
        .owl-next {
            position: absolute;
            top: 45%;
            right: 15px;
        }

        .owl-prev {
            position: absolute;
            top: 45%;
            left: 15px;
        }

        .owl-prev,
        .owl-next {
            width: 45px;
            height: 45px;
            background: #fff !important;
            border: 1px solid #ccc !important;
            color: $dark-text-color !important;
            font-size: 24px !important;

            &:hover,
            &:focus {
                outline: none;
                background: #eee !important;
            }

            i {
                margin: 0px;
            }
        }
    }

    &-caption {
        padding: 10px 30px 20px 30px;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        display: none;
        @include breakpoint(sm){
            display: block;
        }
        > div{
            position: relative;
            z-index: 2;
            h1, h2, h3, h4, p{
                padding: 5px 0px;
                color: #fff;
            }
        }
        &:after {
            content: "";
            background: $dark-background;
            position: absolute;
            bottom: 0;
            top: 0;
            right: 0;
            left: 0;
            opacity: 0.7;
            z-index: 1;
            width: 100%;
            height: 100%;
        }
    }
}

.nice-select {
    clear: none;
}

.select2 {
    &-dropdown {
        border: 1px solid #ccc;
    }

    &.select2-container {
        width: 100% !important;
        padding: 0;
    }

    &-selection__rendered {
        i.currency-flag{
            vertical-align: middle;
            margin-bottom: 4px;
        }
    }
}
.select2-container--focus {
    .select2-selection--single {
        border-color: $primary-color !important;
        box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px lighten($primary-color, 25%);
    }
}
.select2-container--default {
    .select2-search--dropdown .select2-search__field {
        outline: none;
        border-radius: 4px;
        &:focus {
            border-color: $primary-color;
            box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px lighten($primary-color, 25%);
        }
    }

    .select2-selection--single {
        height: 34px;
        border: 1px solid #ccc;
        outline: none;

        .select2-selection__rendered {
            line-height: 32px;
        }

        .select2-selection__arrow {
            top: 4px;
        }

        &.error {
            border-color: $danger-color;
            box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px lighten($danger-color, 20%);
        }
    }
}
select[readonly].select2-hidden-accessible + .select2-container {
    pointer-events: none;
    touch-action: none;

    .select2-selection {
        background: #eee;
        box-shadow: none;
    }



    .select2-selection__arrow,
    .select2-selection__clear {
        display: none;
    }
}

.swal2 {
    &-styled:focus {
        box-shadow: none !important;
    }

    &-popup {
        font-size: 1.3rem !important;
    }
}

.toast {
    opacity: 1;
    -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
    filter: alpha(opacity=100);
}
#toast-container > div {
    opacity: 1;
    -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
    filter: alpha(opacity=100);
}

.wizard {
    > .content {
        background: #fff;

        > .body {
            width: 100%;
            height: auto;
            position: initial;
            padding: 25px 5px;
        }
    }

    > .steps {
        a,
        a:hover,
        a:active {
            height: 70px;
            line-height: 70px;
            border-radius: 4px;
            font-family: $text-font;
            font-weight: $sm-font-weight;
            font-size: $md-font-size;
            text-transform: uppercase;
            position: relative;
            padding: 0px 0px 0px 55px;
        }

        .number {
            font-family: $title-font;
            font-size: $xl-font-size;
            font-weight: $lg-font-weight;
            opacity: 0.3;
            position: absolute;
            top: -2px;
            left: 20px;
        }

        .current a,
        .current a:hover,
        .current a:active {
            background: $primary-color;
        }

        .done a,
        .done a:hover,
        .done a:active {
            background: lighten($primary-color, 15%);
        }

    }
}

.bootstrap-datetimepicker-widget {
    border-radius: 0px;
    border: none;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.2);

    .timepicker {
        &-picker {
            table {
                td {
                    padding: 0px;
                    height: auto;
                    line-height: normal;
                    width: auto;
                }
            }

            .btn {
                span {
                    height: 35px;
                    line-height: 35px;
                }
            }
        }
    }
}

.SumoSelect {
    width: 100%;

    &:focus > .CaptionCont,
    &:hover > .CaptionCont,
    &.open > .CaptionCont {
        border-color: $primary-color;
        box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px lighten($primary-color, 25%);

        &.error {
            border-color: $danger-color ;
            box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px lighten($danger-color, 20%);
        }
    }

    > .CaptionCont {
        width: 100%;
        height: 34px;
        padding: 6px 12px;
        border: 1px solid #ccc;
        border-radius: 4px;
        overflow: hidden;

        &.error {
            border-color: $danger-color;
            box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px lighten($danger-color, 20%);
        }

        > label {
            margin: 0px;
        }

        > span.placeholder {
            color: #999;
            font-style: normal;
        }
    }

    > .optWrapper.multiple > .options li.opt.selected span i,
    .select-all.selected > span i,
    .select-all.partial > span i .select-all {
        background-color: $primary-color;
    }

    .select-all {
        height: auto;
        padding: 8px 0 8px 35px;

        > label {
            margin: 0px;
        }
    }

    .optWrapper > .options li label {
        margin: 0px;
    }
}


.jstree {
    &-icon.jstree-themeicon-custom {
        color: #FD9A17;
        font-size: 20px;
    }

    &-node {
        .jstree-leaf {
            margin-left: 10px !important;

            .jstree-icon.jstree-themeicon-custom {
                color: #FD9A17;
                font-size: 20px;
            }
        }
    }
}
a.act-more {
    text-decoration: none !important;
    font-size: 16px;
    vertical-align: middle;
}

@for $i from 0 through 6{
    .act-tr-level-#{$i} {
        td:first-child {
            white-space: nowrap;
            padding-left: (($i+1) * 10px) !important;
        }
    }
}
