.navbar.navbar-inverse {
    @include background-gradient("horizontal", generate-gradient($primary-color), $primary-color);
    border: none;
    border-radius: 0px;
    margin-bottom: 0px;
    z-index: 1;

    .navbar-header {
        display: inline-block;
        margin: 0px;
    }

    .nav.navbar-nav {
        float: right;

        > li {
            display: inline-block;

            @include breakpoint(sm) {
            }

            > a {
                color: #fff;
            }

            &.dropdown {
                position: static;

                @include breakpoint(sm) {
                    position: relative;
                }

                > .dropdown-menu {
                    border-color: #eee;
                    border-radius: 0px;
                    position: absolute;
                    width: 100%;
                    background: #fff;

                    @include breakpoint(sm) {
                        min-width: 160px;
                    }

                    li {
                        &.divider {
                            background-color: #e5e5e5;
                        }

                        a {
                            background-color: #fff;
                            color: $text-color;

                            &:hover,
                            &:active {
                                background-color: #eee;
                            }
                        }

                        form > button {
                            width: 100%;
                            border: none;
                            display: block;
                            padding: 3px 20px;
                            clear: both;
                            font-weight: 400;
                            text-align: left;
                            line-height: 1.42857143;
                            white-space: nowrap;
                            color: #3D4246;
                            background-color: #fff;

                            &:hover,
                            &:active {
                                text-decoration: none;
                                background-color: #eee;
                            }
                        }
                    }
                }
            }

            &.dropdown.open > a {
                background-color: darken($primary-color, 10%);
            }
        }
    }

    .navbar-form {
        border: none;
        box-shadow: none;

        .input-group {
            .form-control {
                background: darken($primary-color, 15%);
                border: none;
                box-shadow: none;
                color: #fff;
                @include input-placeholder(#f9f9f9);
            }

            .input-group-btn {
                .btn-primary {
                    height: 34px;
                    line-height: 20px;
                    background-color: darken($primary-color, 15%) !important;
                    border: 1px solid darken($primary-color, 15%) !important;

                    i {
                        margin: 0px;
                        font-size: 16px;
                    }
                }
            }
        }
    }
}

.navbar.navbar-default {
    background: #fff;
    box-shadow: 0px 1px 4px 0px #ddd;
    border: none;
    border-radius: 0px;
    margin-bottom: 0px;

    .nav.navbar-nav {
        li {
            a {
                font-size: $sm-font-size;
                font-weight: $md-font-weight;
                color: $dark-text-color;

                &:hover,
                &:focus {
                    color: $primary-color;
                }
            }

            &.active a {
                color: $primary-color;
                background: transparent;
                position: relative;

                &:after {
                    content: "";
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    background-color: $primary-color;
                    height: 3px;
                    border-radius: 10px;
                    width: 70%;
                    margin: auto;
                }
            }
        }
    }
}

.navbar-header {
    .navbar-brand {
        height: auto;
        margin: 3px 0px 0px 0px !important;
        padding: 0px;

        img {
        }
    }
}

.page-header {
    border-color: $divider-color;
    box-shadow: 0px 1px 0px 0px #fff;
    margin: 5px 0px 15px 0px;

    h1 {
        font-family: $heading-font;
        font-weight: $sm-font-weight;
        font-size: $lg-font-size;
        margin: 0px;
        padding: 10px 0px;
    }

    .btn,
    .nav.nav-pills {
        margin-top: 25px;
    }
}

.breadcrumb {
    background: transparent;
    margin-bottom: 5px;
    padding: 0px;
    font-size: $sm-font-size;
    color: $dark-text-color;

    .active {
        color: $dark-text-color;
    }
}

.badge {
    background-color: $accent-color;
}

.form {
    &-control {
        &:focus {
            border-color: $primary-color;
            box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px lighten($primary-color, 25%);
        }

        &[readonly] {
            background-color: #fff;
        }
    }
}

.nav-pills {
    li {
        > a {
            background-color: #fff;
            border-radius: 0px;
            text-transform: uppercase;
        }

        &.active > a {
            background-color: $primary-color;

            &:hover,
            &:focus,
            &:active {
                background-color: darken($primary-color, 5%);
            }
        }
    }
}

.input-group {
    &-addon {
        background: #fff;
        border-right: none
    }

    .form-control {
        border-left: none;
        box-shadow: none;
    }
}

.btn {
    &-lg {
        padding: 10px 12px;
        font-size: 14px;
        border-radius: 4px;
    }

    &-sm {
        padding: 5px 10px;
        font-size: 12px;
        border-radius: 4px;
    }

    &-default {
        @include btn($default-color);
        color: #A1A4AB;
    }

    &-primary {
        @include btn($primary-color);
    }

    &-info {
        @include btn($info-color);
    }

    &-success {
        @include btn($success-color);
    }

    &-warning {
        @include btn($warning-color);
    }

    &-danger {
        @include btn($danger-color);
    }

    &-link {
        color: $primary-color;

        &:hover,
        &:focus {
            color: $primary-color;
        }
    }
}

.label {
    &-default {
        @include label($accent-color, #fff);
    }

    &-primary {
        @include label($primary-color, #fff);
    }

    &-info {
        @include label($info-color, #fff);
    }

    &-success {
        @include label($success-color, #fff);
    }

    &-warning {
        @include label($warning-color, #fff);
    }

    &-danger {
        @include label($danger-color, #fff);
    }
}

.label-outline {
    &-default {
        @include label-round($accent-color);
    }

    &-primary {
        @include label-round($primary-color);
    }

    &-info {
        @include label-round($info-color);
    }

    &-success {
        @include label-round($success-color);
    }

    &-warning {
        @include label-round($warning-color);
    }

    &-danger {
        @include label-round($danger-color);
    }
}

a.label,
a.label-outline {
    &-default {
        @include label-link($accent-color, #fff);
    }

    &-primary {
        @include label-link($primary-color, #fff);
    }

    &-info {
        @include label-link($info-color, #fff);
    }

    &-success {
        @include label-link($success-color, #fff);
    }

    &-warning {
        @include label-link($warning-color, #fff);
    }

    &-danger {
        @include label-link($danger-color, #fff);
    }
}

.panel {
    border: none;
    border-radius: 0px;
    box-shadow: 0px 0px 4px 1px #ddd;

    .panel-heading {
        border-color: #ddd;
        border-radius: 0px;
        background-color: #fff;
        position: relative;

        .panel-title {
            color: $dark-text-color;
            padding: 4px 0px;
            font-family: $heading-font;
            font-size: $md-font-size;
            font-weight: $md-font-weight;

            &:after {
                content: "";
                position: absolute;
                bottom: -2px;
                left: 10px;
                width: 45px;
                height: 4px;
                border-radius: 4px;
            }
        }
    }

    .panel-body {
        p {
            margin: 0px;
        }
    }

    .panel-footer {
        background-color: #fff;

        p {
            margin: 0px;
            font-size: $xs-font-size;
        }
    }

    &.panel-default {
        border: none;

        .panel-heading .panel-title:after {
            background-color: $accent-color;
        }
    }

    &.panel-primary {
        border: none;

        .panel-heading .panel-title:after {
            background-color: $primary-color;
        }
    }

    &.panel-success {
        border: none;

        .panel-heading .panel-title:after {
            background-color: $success-color;
        }
    }

    &.panel-warning {
        border: none;

        .panel-heading .panel-title:after {
            background-color: $warning-color;
        }
    }

    &.panel-danger {
        border: none;

        .panel-heading .panel-title:after {
            background-color: $danger-color;
        }
    }

    &.panel-info {
        border: none;

        .panel-heading .panel-title:after {
            background-color: $info-color;
        }
    }
}

.alert {
    border-radius: 0px;

    &.alert-success {
        @include alert($success-color);
    }

    &.alert-warning {
        @include alert($warning-color);
    }

    &.alert-danger {
        @include alert($danger-color);
    }

    &.alert-info {
        @include alert($info-color);
    }
}

.well {
    background: $light-background;
    border: none;
    box-shadow: none;
    border-radius: 0px;
    margin-bottom: 0px;
    position: relative;
}

.progress {
    height: 5px;
    border-radius: 0px;

    &-bar {
        &-success {
            background-color: $success-color;
            /*@include background-gradient("horizontal", generate-gradient($success-color), $success-color);*/
        }

        &-danger {
            background-color: $danger-color;
            /*@include background-gradient("horizontal", generate-gradient($danger-color), $danger-color);*/
        }

        &-warning {
            background-color: $warning-color;
            /*@include background-gradient("horizontal", generate-gradient($warning-color), $warning-color);*/
        }

        &-info {
            background-color: $info-color;
            /*@include background-gradient("horizontal", generate-gradient($info-color), $info-color);*/
        }

        &-primary {
            background-color: $primary-color;
            /*@include background-gradient("horizontal", generate-gradient($primary-color), $primary-color);*/
        }

        &-default {
            background-color: $accent-color;
            /*@include background-gradient("horizontal", generate-gradient($accent-color), $accent-color);*/
        }
    }
}

.pagination {
    li a {
        color: $primary-color;

        &:hover,
        &:focus {
            color: darken($primary-color, 10%);
        }
    }
}

.media {
    border-bottom: 1px solid #eee;
    padding-bottom: 15px;

    &-heading {
        margin-bottom: 0px;
    }

    &:last-child {
        border-bottom: none;
    }
}

.radio label,
.checkbox label {
    padding-left: 25px;
    font-size: $sm-font-size;
    user-select: none;
}

.text {
    &-primary {
        color: $primary-color;
    }

    &-info {
        color: $info-color;
    }

    &-success {
        color: $success-color;
    }

    &-warning {
        color: $warning-color;
    }

    &-danger {
        color: $danger-color;
    }

    &-default {
        color: $accent-color;
    }
}

.table {
    &-responsive {
        border: none !important;
    }

    > tbody > tr.success > td,
    > tbody > tr.success > th,
    > tbody > tr > td.success,
    > tbody > tr > th.success,
    > tfoot > tr.success > td,
    > tfoot > tr.success > th,
    > tfoot > tr > td.success,
    > tfoot > tr > th.success,
    > thead > tr.success > td,
    > thead > tr.success > th,
    > thead > tr > td.success,
    > thead > tr > th.success {
        background-color: lighten($success-color, 35%)
    }

    > tbody > tr.warning > td,
    > tbody > tr.warning > th,
    > tbody > tr > td.warning,
    > tbody > tr > th.warning,
    > tfoot > tr.warning > td,
    > tfoot > tr.warning > th,
    > tfoot > tr > td.warning,
    > tfoot > tr > th.warning,
    > thead > tr.warning > td,
    > thead > tr.warning > th,
    > thead > tr > td.warning,
    > thead > tr > th.warning {
        background-color: lighten($warning-color, 40%)
    }

    > tbody > tr.danger > td,
    > tbody > tr.danger > th,
    > tbody > tr > td.danger,
    > tbody > tr > th.danger,
    > tfoot > tr.danger > td,
    > tfoot > tr.danger > th,
    > tfoot > tr > td.danger,
    > tfoot > tr > th.danger,
    > thead > tr.danger > td,
    > thead > tr.danger > th,
    > thead > tr > td.danger,
    > thead > tr > th.danger {
        background-color: lighten($danger-color, 40%)
    }

    > tbody > tr.info > td,
    > tbody > tr.info > th,
    > tbody > tr > td.info,
    > tbody > tr > th.info,
    > tfoot > tr.info > td,
    > tfoot > tr.info > th,
    > tfoot > tr > td.info,
    > tfoot > tr > th.info,
    > thead > tr.info > td,
    > thead > tr.info > th,
    > thead > tr > td.info,
    > thead > tr > th.info {
        background-color: lighten($info-color, 40%)
    }
}

.modal-dialog {
    .modal-content {
        border-radius: 0px;

        .modal-header {
            position: relative;

            .modal-title {
                margin: 0px;
                padding: 0px;
                font-size: $md-font-size;
                font-family: $heading-font;
                font-weight: $md-font-weight;

                &:after {
                    content: "";
                    position: absolute;
                    bottom: -2px;
                    left: 15px;
                    width: 45px;
                    height: 4px;
                    border-radius: 4px;
                    background-color: $primary-color;
                }
            }
        }
    }
}

.dropdown {
    &-menu {
        border-radius: 0px;
        border: none;
        box-shadow: 0px 2px 10px 2px rgba(0,0,0,.175);
    }
}

.popover {
    &-title {
        font-family: $heading-font;
        font-weight: $md-font-weight;
        font-size: $xs-font-size;
    }

    &-content {
        font-family: $text-font;
        font-weight: $sm-font-weight;
        font-size: $xs-font-size;
    }
}

.nav-tabs {
    li {
        background: transparent;

        > a {
            background-color: transparent !important;
            border: none;
            border-radius: 0px;
            color: $text-color;
            text-transform: uppercase;
            font-family: $text-font;
            font-size: $sm-font-size;
            font-weight: $md-font-weight;

            &:hover,
            &:focus {
                background-color: transparent !important;
                border: none;
            }
        }

        &.active > a {
            background-color: transparent !important;
            border: none;
            color: $primary-color !important;
            position: relative;

            &:hover,
            &:focus {
                background-color: transparent !important;
                border: none;
            }

            &:after {
                content: "";
                width: 65%;
                height: 3px;
                background-color: $primary-color;
                border-radius: 2px;
                position: absolute;
                bottom: 0;
                right: 0;
                left: 0;
                margin: auto;
            }
        }
    }
}

.tab-content {
    padding: 30px 0px;
}

.nav-pills {
    li {
        > a {
            font-size: $xs-font-size;
            background-color: #fff;
            border-radius: 0px;
            text-transform: uppercase;
        }

        &.active > a {
            background-color: $primary-color;

            &:hover,
            &:focus,
            &:active {
                background-color: darken($primary-color, 5%);
            }
        }
    }
}
