@mixin btn($btn-background) {
    background-color: $btn-background;
    border-color: $btn-background;
    border-bottom: 3px solid darken($btn-background, 10%);
    font-weight: $sm-font-weight;
    text-transform: uppercase;
    color: #fff;

    &:focus,
    &:hover,
    &:active {
        outline: none;
        background-color: darken($btn-background, 5%) !important;
        border-color: darken($btn-background, 5%) !important;
        border-bottom: 3px solid darken($btn-background, 10%) !important;
    }

    &.active,
    &.focus {
        outline: none;
        background-color: darken($btn-background, 15%);
        border-color: darken($btn-background, 15%);
        border-bottom: 3px solid darken($btn-background, 20%);

        &:focus,
        &:hover,
        &:active {
            background-color: darken($btn-background, 20%) !important;
            border-color: darken($btn-background, 20%) !important;
            border-bottom: 3px solid darken($btn-background, 30%) !important;
        }
    }
}

@mixin btn-round-fill($btn-background) {
    padding: 0px;
    text-align: center;
    background: $btn-background;
    border-radius: 100%;
    border: 2px solid $btn-background;
    color: #fff;

    &:hover,
    &:focus {
        background: lighten($btn-background, 5%) !important;
        border: 2px solid $btn-background !important;
        color: #fff !important;
    }
}

@mixin btn-round($btn-background) {
    padding: 0px;
    text-align: center;
    background: transparent;
    border-radius: 100%;
    border: 1px solid $btn-background;
    color: $btn-background;

    &:hover,
    &:focus {
        background: lighten($btn-background, 5%) !important;
        border: 1px solid $btn-background !important;
        color: #fff !important;
    }
}

@mixin alert($alert-color){
  border-left: 4px solid $alert-color;
  border-color: $alert-color;
  color: darken($alert-color, 5%);
  background: lighten($alert-color, 40%);
  position: relative;
  strong{
    display: block;
    text-transform: uppercase;
    font-size: $md-font-size;
  }
  .close{
    position: absolute;
    top: 10px;
    right: 15px;
  }
}

@mixin label($label-background, $label-color) {
    background-color: $label-background;
    border: 1px solid $label-background;
    border-radius: 15px;
    padding: 2px 20px;
    text-transform: uppercase;
    font-size: $xs-font-size;
    font-weight: $xs-font-weight;
}

@mixin label-round($label-color) {
    background: transparent;
    border: 1px solid $label-color;
    border-radius: 15px;
    padding: 2px 20px;
    text-transform: uppercase;
    font-size: $xs-font-size;
    font-weight: $xs-font-weight;
    color: $label-color;
}

@mixin label-link($label-background, $label-color) {
    &:focus,
    &:hover,
    &:active {
        outline: none;
        background-color: darken($label-background, 5%) !important;
        border-color: darken($label-background, 5%) !important;
    }
}
