.header {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 10;
    width: 100%;

    .dropdown {
        &-user {
            > a {
                text-transform: uppercase;

                > i {
                    font-size: 26px;
                    vertical-align: middle;
                }
            }
        }

        &-icon {
            > a {
                position: relative;

                > i {
                    margin: 0px;
                    font-size: 20px;
                    vertical-align: middle;
                }

                .badge {
                    position: absolute;
                    top: 6px;
                    right: 0px;
                }
            }

            .list-group {
                width: 100%;
                padding: 0px;
                margin: 0px;

                @include breakpoint(sm) {
                    width: 300px;
                }

                .list-group-item {
                    border: 0px;
                    border-bottom: 1px solid #eee;
                    margin: 0px;

                    &:first-child,
                    &:last-child {
                        border-radius: 0px;
                    }

                    &:last-child {
                        border-bottom: 0px;
                    }
                }
            }
        }

        &-notification {
            width: 400px !important;
            padding: 0px !important;

            &-view {
                border-top: 1px solid #eee;
                display: block;
                padding: 10px 0px;
                text-align: center;
                text-transform: uppercase;
                color: $light-text-color;

                &:focus,
                &:hover {
                    color: $dark-text-color;
                    text-decoration: none;
                }
            }

            &-slimscroll,
            .slimScrollDiv {
                max-height: 390px !important;
                min-height: 390px !important;
            }
        }
    }
}

.sidemenu {
    &-icon {
        width: 20px;
        height: 17px;
        float: left;
        margin-right: 15px;
        margin-top: 15px;
        position: relative;

        &-bar {
            display: block;
            width: 20px;
            height: 2px;
            position: absolute;
            background-color: #fff;
            transition: 0.4s;

            &:first-child {
                top: 5px;
            }

            &:nth-child(2) {
                bottom: 5px;
            }

            &:last-child {
                bottom: 0;
            }
        }

        &.icon-close {
            .sidemenu-icon-bar:first-child {
                transform: rotate(-42deg) translate(0px, 8px);
            }

            .sidemenu-icon-bar:nth-child(2) {
                opacity: 0;
            }

            .sidemenu-icon-bar:last-child {
                bottom: -2px;
                transform: rotate(45deg) translate(0px,-8px);
            }
        }

        &.icon-back {
            .sidemenu-icon-bar:first-child {
                width: 10px;
                top: 6px;
                transform: rotate(135deg);
            }

            .sidemenu-icon-bar:last-child {
                width: 10px;
                bottom: 1px;
                transform: rotate(-135deg);
            }
        }
    }

    &-navbar {
        height: calc(100% - 45px);
        position: fixed;
        left: 0;
        top: 50px;
        width: 225px;
        z-index: 9;
    }

    &-list {
        margin-top: 15px;

        li {
            margin: 1px 0px;
            box-shadow: none;

            a {
                padding: 10px 15px 10px 45px;
                font-size: $sm-font-size;
                font-weight: $md-font-weight;
                color: $dark-text-color;
                position: relative;

                i {
                    margin: 0px;
                    display: inline-block;
                    font-size: 20px;
                    vertical-align: middle;
                    color: $light-text-color;
                    position: absolute;
                    left: 15px;
                    top: 8px;
                }

                &:hover,
                &:focus {
                    background-color: $light-background;
                    color: $primary-color;

                    > i {
                        color: $primary-color;
                    }
                }
            }

            &.active > a {
                color: $primary-color;
                background-color: $light-background;
                position: relative;

                i {
                    color: $primary-color;
                }

                &:after {
                    content: "";
                    position: absolute;
                    bottom: 0;
                    top: 0;
                    left: 0;
                    background-color: $primary-color;
                    width: 4px;
                    margin: auto;
                }

                &:hover,
                &:focus {
                    background-color: $light-background;
                }
            }

            &.panel {
                > a {
                    position: relative;
                    padding-right: 25px;

                    &:after {
                        position: absolute;
                        right: 0;
                        right: 11px;
                        top: 21px;
                        content: "";
                        display: inline-block;
                        width: 0;
                        height: 0;
                        margin-left: 10px;
                        vertical-align: middle;
                        border-top: 4px dashed;
                        border-right: 4px solid transparent;
                        border-left: 4px solid transparent;
                    }
                }

                ul.nav.nav-stacked {
                    li {
                        a {
                            font-size: $xs-font-size;
                            padding: 8px 2px 8px 55px;

                            i {
                                left: 30px;
                                font-size: 16px;
                                top: 6px;
                            }
                        }

                        &.panel > a:after {
                            top: 15px;
                        }

                        ul.nav.nav-stacked {
                            li {
                                a {
                                    padding: 8px 2px 8px 65px;
                                }

                                i {
                                    left: 40px;
                                }
                            }
                        }
                    }
                }
            }
        }

        .divider {
            height: 1px;
            margin: 9px 0;
            overflow: hidden;
            background-color: #e5e5e5;
        }
    }

    &-content {
        margin-left: 225px;
        margin-top: 115px;
        padding: 15px 25px;

        @include breakpoint(sm) {
            margin-top: 50px;
        }
    }
}

