.btn-round {
    height: 40px;
    width: 40px;
    line-height: 38px;

    i {
        margin: 0px;
        font-size: 16px;
    }

    &.btn-lg {
        height: 45px;
        width: 45px;
        line-height: 42px;

        i {
            margin: 0px;
            font-size: 20px;
        }
    }

    &.btn-md {
        height: 40px;
        width: 40px;
        line-height: 38px;
    }

    &.btn-sm {
        height: 30px;
        width: 30px;
        line-height: 28px;

        i {
            font-size: 14px;
        }
    }

    &.btn-default {
        @include btn-round($default-color);
    }

    &.btn-primary {
        @include btn-round($primary-color);
    }

    &.btn-success {
        @include btn-round($success-color);
    }

    &.btn-info {
        @include btn-round($info-color);
    }

    &.btn-warning {
        @include btn-round($warning-color);
    }

    &.btn-danger {
        @include btn-round($danger-color);
    }

    &.btn-link {
        @include btn-round(#ddd);
        color: $primary-color !important;

        &:hover,
        &:focus {
            color: $primary-color !important;
        }
    }
}

