.login {
    &-body {
        background-image: url('../img/login-bg.jpg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        position: relative;

        &:before {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            width: 100%;
            height: 100%;
            @include background-gradient("vertical", $primary-color, generate-gradient($primary-color));
            opacity: 0.5;
            z-index: 1;
        }
    }

    &-wrapper {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 2;
    }

    &-row {
        width: 100%;
        display: flex;
        flex-direction: row;
        box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2);
    }

    &-col {
        width: 100%;
        height: 475px;
        background: #fff;

        @include breakpoint(sm) {
            width: 50%;
        }
    }

    &-title {
        width: 100%;
        display: inline-block;
        font-family: $heading-font;
        font-size: $lg-font-size;
        font-weight: $sm-font-weight;
        margin: 0px 0px 15px 0px;
        position: relative;

        strong {
            font-family: $text-font;
            font-weight: $xs-font-weight;
            margin-bottom: 5px;
            display: block;
            text-transform: uppercase;
        }

        &:after {
            content: "";
            position: absolute;
            border-radius: 4px;
            bottom: 0;
            left: 0;
            display: inline-block;
            background-color: $primary-color;
            width: 40px;
            height: 4px;
        }
    }

    &-form {
        padding: 75px 25px;

        @include breakpoint(sm) {
            padding: 75px 100px;
        }

        img {
            width: 200px;
        }
    }

    &-banner {
        background-color: lighten($primary-color, 35%);
        background-image: url('../img/login-img.png');
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        display: none;

        @include breakpoint(sm) {
            display: block;
        }
    }
}

.dashboard {
    &-filter {
        margin-bottom: -9px;

        .nav-pills {
            margin-top: 8px !important;

            li {
                background: transparent;

                > a {
                    background-color: $light-background !important;
                    color: $text-color;
                    text-transform: uppercase;
                    font-family: $title-font;
                    font-weight: $md-font-weight;
                }

                &.active > a {
                    background-color: $light-background !important;
                    color: $primary-color !important;
                    position: relative;

                    &:after {
                        content: "";
                        width: 80%;
                        height: 3px;
                        background-color: $primary-color;
                        border-radius: 2px;
                        position: absolute;
                        bottom: 0;
                        right: 0;
                        left: 0;
                        margin: auto;
                    }
                }
            }
        }
    }

    &-tab {
        li {
            > a {
                background-color: $light-background;
                color: $text-color;
                border-right: 1px solid #ddd;
            }

            &:first-child > a {
                border-top-left-radius: 5px;
                border-bottom-left-radius: 5px !important;
            }

            &:last-child > a {
                border-top-right-radius: 5px;
                border-bottom-right-radius: 5px !important;
                border-right: none;
            }

            &.active > a,
            &.active > a:focus {
                background-color: $background;
                color: $primary-color;
            }

            &.active > a:hover {
                background-color: darken($background, 2%);
                color: $primary-color;
            }
        }

        &-content {
            /*margin-top: 30px;*/
        }
    }

    &-counter {
        position: relative;

        &-icon {
            float: right;
            height: 60px;
            width: 60px;
            font-size: 30px;
            text-align: center;
            line-height: 60px;
            border-radius: 6px;
            position: absolute;
            right: 0px;
            top: 0px;
            color: #fff;

            i {
                margin: auto;
            }
        }

        &-title {
            display: block;
            font-family: $heading-font;
            font-size: $sm-font-size;
        }

        &-data {
            display: block;
            font-family: $title-font;
            font-weight: $lg-font-weight;
            font-size: $xl-font-size;
        }
    }

    &-wrapper {
        > div {
            border-right: 1px solid $divider-color;

            &:last-child {
                border-right: none;
            }
        }
    }

    &-block {
        margin: 5px 0px;
        display: block;
    }

    &-title {
        display: block;
        margin-bottom: 5px;
        text-transform: uppercase;
        font-size: $sm-font-size;
        font-weight: $sm-font-weight;
    }

    &-count {
        margin-top: 5px;
        margin-block: 10px;
        margin-right: 10px;
        font-size: $xl-font-size;
        font-family: $title-font;
        font-weight: $lg-font-weight;
        display: inline-block;

        &-lg {
            font-size: $lg-font-size;
            font-family: $title-font;
            font-weight: $lg-font-weight;
            display: inline-block;
        }

        &-md {
            font-size: $md-font-size;
            font-family: $text-font;
            font-weight: $lg-font-weight;
            display: inline-block;
        }
    }

    &-icon {
        padding: 4px 8px;
        font-size: 16px;
        color: #fff;
        display: inline-block;
        border-radius: 4px;
        margin-right: 5px;

        i {
            margin: 0px;
        }
    }

    &-text {
        font-size: $sm-font-size;

        &-lg {
            font-size: $lg-font-size;
        }

        &-md {
            font-size: $md-font-size;
        }

        &-sm {
            font-size: $xs-font-size;
        }
    }

    &-group {
        margin-top: 15px;

        .progress {
            margin-top: 5px;
        }
    }
}

.sidebar {
    &-header {
        padding: 10px 15px;
        border-bottom: 1px solid #ddd;
        position: relative;

        .close {
            position: absolute;
            right: 15px;
            top: 12px;
        }
    }

    &-title {
        color: #2C343F;
        padding: 4px 0;
        font-size: 16px;
        font-weight: 600;
        margin-top: 0;
        margin-bottom: 0;
        font-family: $title-font;
        font-size: $sm-font-size;
        text-transform: uppercase;
        position: relative;

        &:after {
            content: "";
            position: absolute;
            bottom: -12px;
            left: 0px;
            width: 45px;
            height: 4px;
            border-radius: 4px;
            background-color: $primary-color;
        }
    }

    &-body {
        padding: 15px;
        height: calc(100% - 45px) !important;
        overflow-y: auto;
    }
}

.timeline {
    position: relative;

    &-list {
        @extend %clearfix;
    }

    &-title {
        padding-left: 85px;
        margin-bottom: 15px;

        &:after {
            content: "";
            width: 12px;
            height: 12px;
            border-radius: 50%;
            background: $dark-text-color;
            position: absolute;
            left: 60px;
            z-index: 1;
            border: 2px solid #fff;
            top: 10px;
        }

        strong {
            font-family: $heading-font;
            display: block;
            text-transform: uppercase;
        }

        small {
            display: block;
            text-transform: uppercase;
        }
    }

    &-text {
        float: left;
        font-size: $xs-font-size;
        font-weight: $md-font-weight;
    }

    &-item {
        padding-left: 85px;
        position: relative;

        h4 {
            font-family: $text-font;
            font-size: $xs-font-size;
            font-weight: $md-font-weight;
        }

        p {
            font-size: $xs-font-size;
            color: $light-text-color;
        }

        &:after {
            content: "";
            width: 12px;
            height: 12px;
            border-radius: 50%;
            position: absolute;
            left: 60px;
            z-index: 1;
            border: 2px solid #fff;
            top: 5px;
        }

        &-primary:after {
            background: $primary-color;
        }

        &-default:after {
            background: $accent-color;
        }

        &-success:after {
            background: $success-color;
        }

        &-warning:after {
            background: $warning-color;
        }

        &-danger:after {
            background: $danger-color;
        }

        &-info:after {
            background: $info-color;
        }
    }

    &:after {
        content: "";
        height: 100%;
        width: 2px;
        position: absolute;
        top: 0px;
        left: 65px;
        background: $divider-color;
    }
}

.loading {
    &:after {
        content: "";
        background-color: #fff;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: 99;
    }

    svg {
        width: 5%;
        margin: auto;
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        z-index: 999;
    }

    .cls-1,
    .cls-2 {
        animation: flicker 1.5s infinite;
    }

    .cls-3 {
        animation: bounce 1.5s linear infinite;
    }
}

@keyframes bounce {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-8px);
    }

    100% {
        transform: translateY(0);
    }
}

@keyframes flicker {
    0% {
        opacity: 1;
    }

    50% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

